import './style.scss';

export default function PragranceTypeItem({
    name = "Citrus",
    img = "/",
    desc = "Nhm hng cam chanh  xut hin t rt lu v c mt trong rt nhiu chai nc hoa. Ngoi cam, chanh l mi hng chnh ca nhm mi ny, nhng hng khc nh cam bergamot, bi, qut cng thng c s dng mang n s ti mi, sng khoi cho dng mi ny. i vi nhm mi ny, dng vo ma h s tuyt vi nht, mang n cm gic mt m, d chu."
}){
    return(
        <div className="pragrance-type-item-container">
            <div className="pti-title-first">
                <div className="pti-background">
                    <img src={img} alt=""></img>
                </div>
                <div className="pti-title">{name}</div>
            </div>
            <div className="pti-content-on-hover">
                <div className="pti-c-title">{name}</div>
                <p className="pti-desc">"{desc}"</p>
            </div>
        </div>
    )
}